export const validMail = (mail) =>
{
    return /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()\.,;\s@\"]+\.{0,1})+([^<>()\.,;:\s@\"]{2,}|[\d\.]+))$/.test(mail);
}

export const validPhone = (phone) =>
{
    return /^[+]{1}996[0-9]{9}$/.test(phone);
}

export const getPrice = (item) =>
{
    let price = ''
    if(store.getState().app.currency==='USD'){
        if(parseInt(store.getState().app.lvl)==2) {
            price = JSON.parse(item.price)[1].price + ' $'
        } else {
            price = JSON.parse(item.price)[0].price + ' $'
        }
    } else if(store.getState().app.currency==='RUB'){
        if(parseInt(store.getState().app.lvl)==2) {
            price = Math.round(parseFloat(JSON.parse(item.price)[1].price) * parseFloat(store.getState().app.currencyCourse[store.getState().app.currency])) + ' руб'
        } else {
            price = Math.round(parseFloat(JSON.parse(item.price)[0].price) * parseFloat(store.getState().app.currencyCourse[store.getState().app.currency])) + ' руб'
        }
    } else if(store.getState().app.currency==='KGS'){
        if(parseInt(store.getState().app.lvl)==2) {
            price = Math.round(parseFloat(JSON.parse(item.price)[1].price) * parseFloat(store.getState().app.currencyCourse[store.getState().app.currency])) + ' сом'
        } else {
            price = Math.round(parseFloat(JSON.parse(item.price)[0].price) * parseFloat(store.getState().app.currencyCourse[store.getState().app.currency])) + ' сом'
        }
    }
    return price;
}

