export const stringifyDate = (dateTime) => {
    let date = dateTime.split('T')[0].split('-')
    date[1]=date[1].substring(0, 2)
    dateTime = date[2]+'.'+date[1]+'.'+date[0];
    return dateTime
}

var trans=[];
var snart=[];

for(var i=0x410;i<=0x44F;i++){
    trans[i]=i-0x350;
    snart[i-0x350] = i;
}

export const getParams = function (url) {
    var params = {};
    var parser = document.createElement('a');
    parser.href = url ? url : window.location.href;
    var query = parser.search.substring(1);
    var vars = query.split('&');
    if (vars.length < 1 || vars[0].length < 1) return params;
    for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split('=');
        params[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1]);
    }
    return params;
};